.InspectioInventorySectionContainer{
    position: relative;
    width: 100%;
    height: 832px;
    border-radius: 20px;
    padding: 16px;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 32px 0px #72729F29;
    overflow: scroll;
}
/* TabsSection-- start */
.TabsSectionContainer{
    width: 100%;
    height: auto;
    /* //styleName: Button Text/BU1/Medium 16px; */
    font-family: Urbanist;
    font-size: 1vw;
    font-weight: 500;
    line-height: 24px;
    text-align: left;

}
.TabsSectionContainer .button{
    max-width: auto;
    height: auto;
    border: none;
    outline: none;
    border-radius: 16px;
    padding: 6px 19px 6px 19px;
    font: inherit;
}
.TabsSectionContainer .button.lybtn{
    background-color: #21296D;
    color: #FFFFFF;
}
.TabsSectionContainer .button.custbtn{
    background-color: #FAFAFA;
    border: 1px solid #E5E5E5;
    color: #18181B;
}
/* InspectionTable--start */
.InspectionTableContainer{
    width: 100%;
    height: 684px;
    border-radius: 12px;
    border: 1px solid #E5E5E5;
    overflow: hidden;
}
.InspectionTableContainer .InspectionTable{
    width: 100%;
    height: auto;
}
.InspectionTableContainer .InspectionTable thead tr{
    width: 100%;
    height: auto;
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm{
    padding: 10px;
    border-bottom: 1px solid #E5E5E5;
    border-right: 1px solid #E5E5E5;
    /* //styleName: Button Text/BU2/Medium 14px; */
    font-family: Urbanist;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    text-align: left;
    color: #21296D;
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(1){
    width: 14.5%;
    /* height: 40px; */
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(2){
    width: 10%;
    /* height: 40px; */
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(3){
    width: 13%;
    /* height: 40px;
    font-size: 0.25vw; */
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(4){
    width: 28%;
    /* height: 40px; */
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(5){
    width: 14.5%;
    /* height: 40px; */
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(6){
    width: 10%;
    /* height: 40px; */
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm:nth-child(7){
    width: 10%;
    /* height: 40px; */
    border-right: none;
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm.DocumentTileRiskItmCol{
    width: calc(28% + 14.5%);
}
.InspectionTableContainer .InspectionTable thead tr .HeaderItm.ExpiryDateRiskItmCol{
    width: 10%;
    /* height: 40px; */
}
.RowItm{
    height: auto;
    overflow: hidden;
    padding: 8px;
    border-bottom: 1px solid #E5E5E5;
    border-right: 1px solid #E5E5E5;
    background-color: #FFFFFF;
    /* //styleName: Button Text/BU2/Medium 14px; */
    font-family: Urbanist;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
}
.DocumentItm{
    width: 14.5%;
    height: auto;
}
.DocumentChip{
    width: auto;
    height: 32px;
    border-radius: 8px;
    border: 1px solid #52525B;
    background-color: #FAFAFA;
    padding: 6px 8px 6px 8px;
}
.DocumentId{
    font: inherit;
    color: #52525B;
}
.DateItm{
    width: 10%;
    /* height: 63px; */
}
.DateText{
    width: auto;
    /* height: 20px; */
    font: inherit;
    color: #18181B;
}
.DateType{
    width: 13%;
    /* height: 63px; */
}
.DocumentTypeText{
    font: inherit;
    font-size: 0.75vw;
    color: inherit;
}
.DateTitle{
    width: 28%;
    /* height: 63px; */
}
.LRSKDocumenteTitle{
    width: 42.5%;
}
.DocumentTitleText{
    font: inherit;
    color: #18181B;
}
.creator{
    width: 14.5%;
    /* height: 63px; */
}
.creator .profileimg{
    width: 32px;
    height: 32px;
    border-radius: 8px;
}
.creator .creatorText{
    font: inherit;
    color: #18181B;
}
.Action{
    width: 10%;
    /* height: 63px; */
    gap:12px;
}
.Icon{
    position: relative;
    width: 20px;
    height: 20px;
}
.SvgIcon{
    position: absolute;
    width: 13.89px;
    height: 15.34px;
    top: 2.29px;
    left: 3.13px;
    border: 1.5px 0px 0px 0px;

}
/* Table--end */
.InspectioInventorySectionContainer .ButtonGroupContainer{
    width: 100%;
    height: auto;
}
.ButtonGroupContainer .BtnPrevious{
    width: auto;
    height: 48px;
    border-radius: 16px;
    padding: 12px 20px 12px 12px;
    border: 1px solid #E5E5E5;
    background-color: #FFFFFF;
    gap: 4px;
}
.ButtonGroupContainer .BtnNext{
    width: auto;
    height: 48px;
    border-radius: 16px;
    padding: 12px 20px 12px 12px;
    border: 1px solid #E5E5E5;
    background-color: #FFFFFF;
    gap: 4px;
}
.ButtonGroupContainer .BtnPrevious .btnText{
    font-family: Urbanist;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    text-align: left;
    color:#18181B;
}
.ButtonGroupContainer .BtnNext  .nxtIconSvg{
    width: 20px;
    height: 20px;
    transform: rotate(180deg);
}
